html, body {
  width: 100%;
  height: 100%;
  display: flex;
  overflow-x: hidden;
  align-items: center;
  justify-content: center;
  background: #0f1217;
}

.cursor_pointer {
  cursor: pointer;
}

#Container {
  width: 37em;
  height: 18em;
  align-items: center;
  border-radius: 10px;
  box-shadow: 25px 10px 100px -75px black;
  background: #1b2129;
}

#ContainerList {
  overflow-x: hidden;
  overflow-y: auto;
  padding: 15px;
}
#ContainerList .ContainerListItem {
  height: 35px;
  padding: 10px;
  display: flex;
  align-items: center;
  border-radius: 10px;
  margin-bottom: 10px;
  background-color: #28323e;
}
#ContainerList .ContainerListItem button:first-of-type {
  transform: translateX(30em);
}
#ContainerList .ContainerListItem button {
  background: #297ff0;
  outline: none;
  border: none;
  height: 3em;
  width: 3em;
  margin-right: 10px;
  border-radius: 8px;
  position: absolute;
  transform: translateX(34em);
}
#ContainerList .ContainerListItem button:last-of-type {
  transform: translateX(37.9em);
}